import { ModalHandlerService } from './../../shared/services/modal-handler/modal-handler.service';
import { Router } from '@angular/router';
import { AlertService } from './../../shared/services/alert/alert.service';
import { RewardService } from './services/reward.service';
import { Component, OnInit } from '@angular/core';
import { GlobalDataService } from 'src/app/shared/services/globalData/global-data.service';
import { Reward } from 'src/app/shared/models/reward.model';
import { RouterConstants } from 'src/app/shared/config/constants/router.constants';
import { IonRouterOutlet } from '@ionic/angular';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-prize-list',
  templateUrl: './prize-list.page.html',
  styleUrls: ['./prize-list.page.scss'],
})
export class PrizeListPage implements OnInit {

  elements: Array<Reward> = new Array<Reward>();
  totalElements: number;
  loadingContent: boolean;
  currentElement: number;
  elementsPerPage: number = 10;

  environment = environment;
  constructor(
    public globalData: GlobalDataService,
    private rewardService: RewardService,
    private router: Router,
  ) { }

  ngOnInit() {
    this.getUserRewards();
  }

  array(n: number): any[]{
    return Array(n);
  }

  async getUserRewards(event?) {
    try {
      if(!event) this.loadingContent = true;
      this.currentElement = this.elements.length;
      const data = await this.rewardService.getUserRewards(this.currentElement, this.elementsPerPage).toPromise();
      this.totalElements = data.count;
      this.elements = this.elements.concat(data.rows);
      console.log('Prizes ', this.elements, this.totalElements);
      if (event) { await event.target.complete(); }
      this.loadingContent = false;
    } catch (error) {
      console.log(error);
      this.loadingContent = false;
    }
  }

  async refreshData(event) {
    this.elements = new Array<Reward>();
    await this.getUserRewards()
    event.target.complete();
  }

  async openDetail(element) {
    console.log('Detail', element);
    this.router.navigateByUrl(RouterConstants.PRIZE_DETAIL+ element.id, {state: element.selectedElement});
  }

}
